export default "import React from 'react';\nimport Badge from '@material-ui/core/Badge';\nimport FormControl from '@material-ui/core/FormControl';\nimport FormControlLabel from '@material-ui/core/FormControlLabel';\nimport FormLabel from '@material-ui/core/FormLabel';\nimport Radio from '@material-ui/core/Radio';\nimport RadioGroup from '@material-ui/core/RadioGroup';\nimport {makeStyles} from '@material-ui/core/styles';\nimport MailIcon from '@material-ui/icons/Mail';\n\nconst useStyles = makeStyles((theme) => ({\n  root: {\n    display: 'flex',\n    flexDirection: 'column',\n  },\n  formControl: {\n    margin: theme.spacing(3),\n  },\n  row: {\n    display: 'flex',\n    justifyContent: 'center',\n  },\n  margin: {\n    margin: theme.spacing(2),\n  },\n}));\n\nexport default function BadgeAlignment() {\n  const classes = useStyles();\n  const [horizontal, setHorizontal] = React.useState('right');\n  const [vertical, setVertical] = React.useState('top');\n\n  function handleHorizontalChange(event) {\n    setHorizontal(event.target.value);\n  }\n\n  function handleVerticalChange(event) {\n    setVertical(event.target.value);\n  }\n\n  return (\n    <div className={classes.root}>\n      <div className={classes.row}>\n        <FormControl component='fieldset' className={classes.formControl}>\n          <FormLabel component='legend'>Vertical</FormLabel>\n          <RadioGroup value={vertical} onChange={handleVerticalChange}>\n            <FormControlLabel value='top' control={<Radio />} label='Top' />\n            <FormControlLabel\n              value='bottom'\n              control={<Radio />}\n              label='Bottom'\n            />\n          </RadioGroup>\n        </FormControl>\n        <FormControl component='fieldset' className={classes.formControl}>\n          <FormLabel component='legend'>Horizontal</FormLabel>\n          <RadioGroup value={horizontal} onChange={handleHorizontalChange}>\n            <FormControlLabel value='right' control={<Radio />} label='Right' />\n            <FormControlLabel value='left' control={<Radio />} label='Left' />\n          </RadioGroup>\n        </FormControl>\n      </div>\n      <div className={classes.row}>\n        <Badge\n          color='secondary'\n          variant='dot'\n          badgeContent={1}\n          anchorOrigin={{\n            horizontal,\n            vertical,\n          }}\n          className={classes.margin}>\n          <MailIcon />\n        </Badge>\n        <Badge\n          color='secondary'\n          badgeContent={1}\n          anchorOrigin={{\n            horizontal,\n            vertical,\n          }}\n          className={classes.margin}>\n          <MailIcon />\n        </Badge>\n        <Badge\n          color='secondary'\n          badgeContent={12}\n          anchorOrigin={{\n            horizontal,\n            vertical,\n          }}\n          className={classes.margin}>\n          <MailIcon />\n        </Badge>\n        <Badge\n          color='secondary'\n          badgeContent={123}\n          anchorOrigin={{\n            horizontal,\n            vertical,\n          }}\n          className={classes.margin}>\n          <MailIcon />\n        </Badge>\n        <Badge\n          color='secondary'\n          max={999}\n          badgeContent={1337}\n          anchorOrigin={{\n            horizontal,\n            vertical,\n          }}\n          className={classes.margin}>\n          <MailIcon />\n        </Badge>\n      </div>\n    </div>\n  );\n}\n";