export default "import React from 'react';\nimport clsx from 'clsx';\nimport Badge from '@material-ui/core/Badge';\nimport FormControl from '@material-ui/core/FormControl';\nimport FormControlLabel from '@material-ui/core/FormControlLabel';\nimport FormLabel from '@material-ui/core/FormLabel';\nimport Radio from '@material-ui/core/Radio';\nimport RadioGroup from '@material-ui/core/RadioGroup';\nimport {makeStyles} from '@material-ui/core/styles';\n\nconst useStyles = makeStyles((theme) => ({\n  formControl: {\n    marginBottom: theme.spacing(3),\n  },\n  row: {\n    display: 'flex',\n    justifyContent: 'center',\n  },\n  shapeContainer: {\n    margin: theme.spacing(2),\n  },\n  shape: {\n    backgroundColor: theme.palette.primary.main,\n    width: 40,\n    height: 40,\n  },\n  shapeCircle: {\n    borderRadius: '50%',\n  },\n}));\n\nexport default function BadgeOverlap() {\n  const classes = useStyles();\n  const [overlap, setOverlap] = React.useState('rectangle');\n\n  function handleChange(event) {\n    setOverlap(event.target.value);\n  }\n\n  return (\n    <div>\n      <div className={classes.row}>\n        <FormControl component='fieldset' className={classes.formControl}>\n          <FormLabel component='legend'>Overlap</FormLabel>\n          <RadioGroup value={overlap} onChange={handleChange}>\n            <FormControlLabel\n              value='rectangle'\n              control={<Radio />}\n              label='Rectangle'\n            />\n            <FormControlLabel\n              value='circle'\n              control={<Radio />}\n              label='Circle'\n            />\n          </RadioGroup>\n        </FormControl>\n      </div>\n      <div className={classes.row}>\n        <div className={classes.shapeContainer}>\n          <Badge\n            color='secondary'\n            overlap={overlap}\n            badgeContent=' '\n            variant='dot'>\n            <div\n              className={clsx(\n                classes.shape,\n                overlap === 'circle' && classes.shapeCircle,\n              )}\n            />\n          </Badge>\n        </div>\n        <div className={classes.shapeContainer}>\n          <Badge color='secondary' overlap={overlap} badgeContent=' '>\n            <div\n              className={clsx(\n                classes.shape,\n                overlap === 'circle' && classes.shapeCircle,\n              )}\n            />\n          </Badge>\n        </div>\n      </div>\n    </div>\n  );\n}\n";