export default "import React from 'react';\nimport {makeStyles} from '@material-ui/core/styles';\nimport Badge from '@material-ui/core/Badge';\nimport IconButton from '@material-ui/core/IconButton';\nimport MailIcon from '@material-ui/icons/Mail';\nimport AppBar from '@material-ui/core/AppBar';\nimport Tabs from '@material-ui/core/Tabs';\nimport Tab from '@material-ui/core/Tab';\nimport Typography from '@material-ui/core/Typography';\nimport Button from '@material-ui/core/Button';\n\nconst useStyles = makeStyles((theme) => ({\n  margin: {\n    margin: theme.spacing(2),\n  },\n  padding: {\n    padding: theme.spacing(0, 2),\n  },\n  tabItem: {\n    minWidth: 100,\n  },\n}));\n\nexport default function SimpleBadge() {\n  const classes = useStyles();\n\n  return (\n    <div>\n      <div>\n        <Badge className={classes.margin} badgeContent={4} color='primary'>\n          <MailIcon />\n        </Badge>\n        <Badge className={classes.margin} badgeContent={10} color='secondary'>\n          <MailIcon />\n        </Badge>\n        <IconButton aria-label='4 pending messages' className={classes.margin}>\n          <Badge badgeContent={4} color='primary'>\n            <MailIcon />\n          </Badge>\n        </IconButton>\n      </div>\n      <AppBar position='static' className={classes.margin}>\n        <Tabs value={0}>\n          <Tab\n            className={classes.tabItem}\n            label={\n              <Badge\n                className={classes.padding}\n                color='secondary'\n                badgeContent={4}>\n                Item One\n              </Badge>\n            }\n          />\n          <Tab className={classes.tabItem} label='Item Two' />\n          <Tab className={classes.tabItem} label='Item Three' />\n        </Tabs>\n      </AppBar>\n      <Badge color='primary' badgeContent={4} className={classes.margin}>\n        <Typography className={classes.padding}>Typography</Typography>\n      </Badge>\n      <Badge color='primary' badgeContent={4} className={classes.margin}>\n        <Button variant='contained'>Button</Button>\n      </Badge>\n    </div>\n  );\n}\n";